import { Component, OnInit } from '@angular/core';
import { NasaService } from '../../services/nasa.service';
@Component({
  selector: 'app-nasa',
  providers: [NasaService],
  templateUrl: './nasa.component.html',
  styleUrls: ['./nasa.component.css']
})
export class NasaComponent implements OnInit {
  photos : Array<any>;
  pic :any ={img_src:'/assets/images/background.jpg'};
  initial: number;
  isLoaded:boolean = false;
  constructor( private _nasaService: NasaService) { }

  ngOnInit() {
    this.initial = Math.floor(Math.random()*600)+1;
    this.getAllItems();
  }

  private getAllItems(): void {
        this._nasaService
            .getData()
            .subscribe(data => {
              this.photos = data.photos.slice(this.initial, this.initial+100);
              console.log(this.photos);
              this.pic = this.photos[0];
              this.isLoaded = true;
            });
    }
}



